import React, { useState } from "react";

function Counter() {
    const [Count, setCount] = useState(0);

    const add = () => {
        setCount(Count + 1);
    };

    const rem = () => {
        setCount(Count - 1);
    };
    const res = () => {
        setCount(0);
    };

    return (
        <div>
            <div className="boton">
                <button
                    onClick={add}
                    style={{
                        width: "25px",
                        height: "32px",
                        margin: "3px",
                    }}
                >
                    +
                </button>
                <button
                    onClick={rem}
                    style={{
                        width: "25px",
                        height: "32px",
                        padding: "1px",
                        margin: "3px",
                    }}
                >
                    -
                </button>
                <button
                    onClick={res}
                    style={{
                        width: "25px",
                        height: "32px",
                        padding: "1px",
                        margin: "3px",
                    }}
                >
                    X
                </button>
            </div>
            <p>{Count}</p>
        </div>
    );
}

export default Counter;
